import React, { useEffect, useState } from "react";
import api from "./api";
import DeviceList from "./components/DeviceList";
import DataChart from "./components/DataChart";
import "./App.css";

function App() {
  const [devices, setDevices] = useState([]);
  const [selectedDevice, setSelectedDevice] = useState(null);
  const [data, setData] = useState([]);

  // Load devices
  useEffect(() => {
    api.get("/devices").then((res) => {
      setDevices(res.data);
      if (res.data.length > 0) {
        setSelectedDevice(res.data[0].id);
      }
    });
  }, []);

  // Load device data
  useEffect(() => {
    if (!selectedDevice) return;

    const fetchData = () => {
      api.get(`/data/${selectedDevice}`).then((res) => {
        setData(res.data);
      });
    };

    fetchData();
    const i = setInterval(fetchData, 5000);
    return () => clearInterval(i);
  }, [selectedDevice]);

  const latest = data.length > 0 ? data[data.length - 1] : null;
  const previous = data.length > 1 ? data[data.length - 2] : null;

  const tempTrend =
    latest && previous
      ? latest.temperature > previous.temperature
        ? "up"
        : "down"
      : null;

  return (
    <div className="dashboard">
      <div className="header">
        <h2>Unified IoT Dashboard</h2>
        <p>Live device telemetry via MQTT</p>
      </div>

      <div className="grid">
        <DeviceList
          devices={devices}
          selectedDevice={selectedDevice}
          onSelect={setSelectedDevice}
        />

        <div className="card">
          <h3>Live Metrics</h3>

          <div className="stats">
            <div className="stat-card temp">
              <p>Temperature</p>
              <h1>
                {latest ? `${latest.temperature}C` : "--"}
                {tempTrend === "up" && " "}
                {tempTrend === "down" && " "}
              </h1>
            </div>

            <div className="stat-card humidity">
              <p>Humidity</p>
              <h1>{latest ? `${latest.humidity}%` : "--"}</h1>
            </div>

            <div className="stat-card time">
              <p>Last Update</p>
              <h2>
                {latest
                  ? new Date(latest.timestamp).toLocaleTimeString()
                  : "--"}
              </h2>
            </div>
          </div>

          <div className="stat-card battery">
            <p>Battery</p>
            <h1>{latest ? `${latest.battery ?? 87}%` : "--"}</h1>
          </div>

          <div className="chart-wrapper">
            {data.length > 0 && <DataChart data={data} />}
          </div>
        </div>
      </div>

      <footer style={{ marginTop: 40, color: "#64748b" }}>
        Unified IoT Dashboard  MQTT + Node + React
        Made by Soham Sachdeva
      </footer>
    </div>
  );
}

export default App;
