function DeviceList({ devices, selectedDevice, onSelect }) {
  return (
    <div className="card">
      <h3>Devices</h3>

      {devices.length === 0 && (
        <p style={{ color: "#9ca3af" }}>No devices found</p>
      )}

      {devices.map((device) => (
        <div
          key={device.id} //  STRING KEY
          className={`device ${
            device.id === selectedDevice ? "active" : ""
          }`}
          onClick={() => onSelect(device.id)} //  ONLY ID
        >
          <span className="dot"></span>

          <div>
            <strong>{device.id}</strong>
            <br />
            <small style={{ color: "#9ca3af" }}>
              {device.location}
            </small>
          </div>
        </div>
      ))}
    </div>
  );
}

export default DeviceList;
